@import url(https://fonts.googleapis.com/css?family=Fredoka+One);
body {
  margin: 0;
  padding: 0;
  font-family: 'Fredoka One',"Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fcfcfc; }
  @media (max-width: 500px) {
    body {
      font-size: 15px; } }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

button {
  background-color: inherit;
  color: white;
  border: 5px solid white;
  border-radius: 100px;
  font-family: inherit;
  font-size: 1.5em;
  padding: 5px;
  transition: all 0.5s;
  min-width: 5em; }

button:hover, button:active {
  font-size: 2em; }

h2 {
  font-weight: inherit; }

a {
  color: #6c5cff; }

.green-text {
  color: green; }

.red-text {
  color: red; }

.intro-screen {
  margin: 0;
  padding: 0 0 0 0;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #6c5cff;
  color: white; }

.intro-screen-content {
  text-align: center;
  padding-bottom: 5em; }

.info {
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  text-align: left;
  max-width: 318px; }

.info p {
  margin-left: 1em; }

.info a {
  color: white;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4); }

.info li {
  margin-top: 1em; }

.loader-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: calc(100vh - 320px); }

.loader {
  border: 16px solid #fcfcfc;
  border-top: 16px solid #6c5cff;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite; }

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.end-screen {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }
  @media (max-width: 700px) {
    .end-screen {
      -webkit-align-items: baseline;
              align-items: baseline; } }

.end-screen .score {
  font-size: 1.5em; }

.end-screen .high-score {
  font-size: 3em; }

.end-screen > * {
  -webkit-flex: 1 1;
          flex: 1 1; }
  @media (max-width: 700px) {
    .end-screen > * {
      min-width: 100%; } }

.gg {
  background-color: #6c5cff;
  height: 100%;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-bottom: 4em;
  box-sizing: border-box; }
  @media (max-width: 700px) {
    .gg {
      height: 50%; } }

.gg h2 {
  font-size: 2em;
  margin: 10px; }

.gg p {
  padding: 0;
  margin: 5px; }

.gg button {
  border-color: white;
  color: white;
  width: 200px;
  margin-top: 20px; }

.review-wrapper {
  -webkit-align-self: baseline;
          align-self: baseline; }
  @media (max-width: 700px) {
    .review-wrapper {
      height: 50%; } }

.review {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 400;
  margin: 20px;
  max-height: 95vh;
  overflow: auto; }
  @media (max-width: 700px) {
    .review {
      max-height: unset; } }

.review table {
  border-spacing: 0 10px; }

.review th p {
  padding: 0;
  margin: 0;
  font-style: italic;
  font-weight: 300; }

.review td {
  width: 50%;
  box-sizing: border-box;
  vertical-align: top;
  padding: 20px; }

.review td + td {
  border-left: 1px solid rgba(0, 0, 0, 0.6); }

.choice {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 1 1;
          flex: 1 1;
  min-width: 320px;
  min-height: 40vh;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #6c5cff;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
  font-size: 1.3em;
  color: white;
  text-transform: capitalize;
  transition: all 0.5s;
  cursor: pointer; }
  @media (min-width: 880px) {
    .choice {
      height: 80vh; } }

.choice:hover {
  background-color: #968aff; }

.choice p {
  margin: 0;
  padding: 50px;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

.round {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  min-width: 320px;
  margin: 0 auto;
  text-align: center;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.8);
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

.score {
  text-align: center;
  font-size: 2em; }

