@import url("https://fonts.googleapis.com/css?family=Fredoka+One");
body {
  margin: 0;
  padding: 0;
  font-family: 'Fredoka One',"Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fcfcfc; }
  @media (max-width: 500px) {
    body {
      font-size: 15px; } }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

button {
  background-color: inherit;
  color: white;
  border: 5px solid white;
  border-radius: 100px;
  font-family: inherit;
  font-size: 1.5em;
  padding: 5px;
  transition: all 0.5s;
  min-width: 5em; }

button:hover, button:active {
  font-size: 2em; }

h2 {
  font-weight: inherit; }

a {
  color: #6c5cff; }

.green-text {
  color: green; }

.red-text {
  color: red; }
